<template>
    <div  class="collapse-trigger" role="button" @click="accordion($event)">
        <div class="card-title-area">
            <h3 class="title"><span class="num">[{{ episode.id }}화]</span> {{ episode.title }}</h3>
            <a :href="episode.podcast" target="_blank" v-if="episode.podcast" class="podcast-link">
                <span class="icon is-medium"><i class="mdi mdi-podcast"></i></span>
            </a>
        </div>
        <div class="card-link-area">
            <div class="link-area">
                <a :href="episode.link" v-if="episode.link" :class="setLinkIconClass(episode.link)" target="_blank"></a>
                <a :href="episode.link2" v-if="episode.link2" :class="setLinkIconClass(episode.link2)" target="_blank"></a>
            </div>
            <div class="card-header-icon" v-if="episode.relatedWorks.length !== 0 || episode.recommendation.length !== 0">
                <span class="icon">
                    <i class="mdi mdi-menu-down mdi-24px"></i>
                </span>
            </div>
        </div>
    </div>
    <div class="collapse-content" v-if="episode.relatedWorks.length !== 0 || episode.recommendation.length !== 0" style="display:none">
        <div class="card-content">
            <h3  v-if="episode.relatedWorks.length !== 0">오늘의 자매들</h3>
            <ul class="related" v-if="episode.relatedWorks.length !== 0">
                <li v-for="(work, i) in episode.relatedWorks" :key="i" class="work-item">
                    <a :href="work.link" target="_blank" :class="setLinkIconClass(work.link)"></a>
                    <h4>{{ work.title }}</h4>
                </li>
            </ul>
            <h3 v-if="episode.recommendation.length !== 0">추천작</h3>
            <ul class="related" v-if="episode.recommendation.length !== 0">
                <li v-for="(work, i) in episode.recommendation" :key="i">
                    <a :href="work.link" target="_blank" :class="setLinkIconClass(work.link)"></a>
                    <h4>{{ work.title }}</h4>
                </li>
            </ul>
        </div>
    </div>
</template>
 
<script>
export default {
    name: 'episode-item',
    props: {
        episode : Object,
    },
    data() {
		return {
        }
    },
    mounted() {
        this.$nextTick(function() {
            this.setLinkIconClass();
        })
    },
    methods: {
        setLinkIconClass(link) {
            let iconClass = '';
            if(link !== null){
                if(link.indexOf('flix') > -1) {
                    iconClass = 'netflix link'
                }else if(link.indexOf('watcha') > -1) {
                    iconClass = 'watcha link'
                }else if(link.indexOf('disney') > -1) {
                    iconClass = 'disney link'
                }else if(link.indexOf('youtube') > -1) {
                    iconClass = 'youtube link'
                }else if(link.indexOf('wavve') > -1) {
                    iconClass = 'wavve link'
                }else if(link.indexOf('tving') > -1){
                    iconClass = 'tving link'
                }else if(link.indexOf('yes24') > -1){
                    iconClass = 'book link'
                }else if(link.indexOf('podbbang') > -1){
                    iconClass = 'podcast link'
                }else {
                    iconClass = 'basic link'
                }
            }
            return iconClass
        },
        accordion(event) {
            let el = event.currentTarget;
            let target = el.nextElementSibling;
            if(target !== null) {
                if(target.style.display == 'none') {
                    target.style.display = 'block';
                    el.querySelector('.mdi-menu-down').classList.replace('mdi-menu-down', 'mdi-menu-up');
                }else {
                    target.style.display = 'none'
                    el.querySelector('.mdi-menu-up').classList.replace('mdi-menu-up', 'mdi-menu-down');
                }
            }
            
        }, 
    }

}
</script>
