<template>
    <div class="top"> 
        <div class="title-area">
            <div class="logo sisterhood">
                <span class="blind">여성이 보는 여성의 이야기 시스터후드</span>
            </div>
            <div class="tit-txt">
                <h1>시스터후드</h1>
                <p class="author">헤이메이트</p>
                <ul class="url-link">
                    <li><a href="https://www.podbbang.com/channels/1771727" class="podbbang" target="_blank"></a></li>
                    <li><a href="https://podcasts.apple.com/us/podcast/%EC%8B%9C%EC%8A%A4%ED%84%B0%ED%9B%84%EB%93%9C/id1462838595" class="apple" target="_blank"></a></li>
                    <li><a href="https://audioclip.naver.com/channels/1111" class="audioclip" target="_blank"></a></li>
                </ul>
            </div>
            <a href="https://docs.google.com/spreadsheets/d/1gbdQ7yUz2crTrhVXFper21OOf12q3V5xRh8EjpI2IYs/edit?usp=sharing" class="btn-data" target="_blank"><i class="mdi mdi-microsoft-excel"></i>데이터 입력</a>
        </div>
    </div>
    <div class="form">
        <div class="input-search">
            <input type="text" v-model="search">
        </div>
       
    </div> 
    <div class="ctrl-area">
        <div class="ctrl-btn">
            <button type="button" class="button latest active" @click="sortListOrder()">
                <span class="icon is-small"><i class="mdi mdi-sort"></i></span>  
                <span class="tx">{{ sortTx }}</span>  
            </button>
        </div>
        <div class="btns-season" id="btnsSeason">
            <a role="button" @click="sortSeason($event, 'all')" class="button">전체</a>
            <a role="button" @click="sortSeason($event, 4)" class="button active">시즌4</a>
            <a role="button" @click="sortSeason($event, 3)" class="button">시즌3</a>
            <a role="button" @click="sortSeason($event, 2)" class="button">시즌2</a>
            <a role="button" @click="sortSeason($event, 1)" class="button">시즌1</a>
        </div>
         <p class="notice">* 구글 시트에서 데이터를 불러오는 데 다소 시간이 걸립니다. 조금만 기다려주세요 ☺️ <br> 
         * 데이터 입력 버튼을 누르시면 데이터 수정 및 추가가 가능합니다. 자유롭게 오타 및 신규 에피소드 정보를 업데이트 해주세요.</p>
    </div>
    <div class="episode-list">
        <div class="card" v-for="(episode, idx) in filteredData" :key="idx" >
            <episode-item :episode="episode"></episode-item>
        </div>
    </div>
</template>

<script>
const { GoogleSpreadsheet } = require('google-spreadsheet');
const creds = require('../client.json');
import episodeitem from './EpisodeItem';

export default {
    name: "Sheet",
    data() {
			return {
                episodes: [],
                episodesLength : 0,
                search: '',
                searchedData: [],
                sortBy: 'latest',
                sortTx : '최신순',
                season: '4',
			}
        },
    components: {
        'episode-item' : episodeitem,
    },
    created() {
        this.accessSpreadSheet();
    },
    computed: {
        filteredData() {
            let listData = this.episodes;
            listData = listData.sort((a, b) => {
                if(this.sortBy == 'latest') {
                    return b.id -  a.id;  
                } else if (this.sortBy == 'oldest') {
                    return a.id - b.id;
                }
            })
            listData = this.episodes.filter(a => {
                let sisData = a.relatedWorks.filter(b => {
                    return b.title.toLowerCase().includes(this.search.toLowerCase())    
                })
                return a.title.toLowerCase().includes(this.search.toLowerCase()) || sisData.length > 0
            })

            return listData;
        }
    }, 
    methods:{
        async accessSpreadSheet() {
            const doc = new GoogleSpreadsheet('1gbdQ7yUz2crTrhVXFper21OOf12q3V5xRh8EjpI2IYs');
            await doc.useServiceAccountAuth(creds);
            await doc.loadInfo(); 
            let sheet,
                sheet2,
                sheet3,
                rows3;
            if(this.season == 1) {
                sheet = doc.sheetsByIndex[2];
                sheet2 = doc.sheetsByIndex[3];
            }else if(this.season == 2){
                sheet = doc.sheetsByIndex[4];
                sheet2 = doc.sheetsByIndex[5];
                sheet3 = doc.sheetsByIndex[6];
                rows3 = await sheet3.getRows({
                    offset: 0
                })
            }else if(this.season == 3){
                sheet = doc.sheetsByIndex[7];
                sheet2 = doc.sheetsByIndex[8];
            }else if(this.season == 4){
                sheet = doc.sheetsByIndex[9];
                sheet2 = doc.sheetsByIndex[10];
            }else if(this.season == 'all') {
                sheet = doc.sheetsByIndex[0];
                sheet2 = doc.sheetsByIndex[1];
                sheet3 = doc.sheetsByIndex[6];
                rows3 = await sheet3.getRows({
                    offset: 0
                })
            }
            const rows = await sheet.getRows({
                offset: 0
            })
            const rows2 = await sheet2.getRows({
                offset: 0
            })
            
            this.episodes = rows;
            this.episodes.forEach(a => {
                a.relatedWorks = []
                a.recommendation = []
                rows2.forEach(b => {
                    if(a.id == b.id){
                        a.relatedWorks.push(b)
                    }
                })
                if(this.season == 2 || this.season == 'all') {
                    rows3.forEach(c => {
                        if(a.id == c.id){
                            a.recommendation.push(c)
                        }
                    })
                }
                
            })
        },
        sortSeason(event, num) {
            this.season = num;
            this.accessSpreadSheet();
            let el = event.currentTarget;
            this.siblings(el).forEach(a => {
                a.classList.remove('active')
            })
            el.classList.add('active');
            
        },
        sortListOrder() {
            if(this.sortBy == 'latest') {
                this.sortTx = '등록순'
                this.sortBy = 'oldest'
            }else {
                this.sortTx = '최신순'
                this.sortBy = 'latest'
            }
        },
        siblings(t) {
            var children = t.parentElement.children;
            var tempArr = [];

            for (var i = 0; i < children.length; i++) {
                tempArr.push(children[i]);
            }

            return tempArr.filter(function(e){
                return e != t;
            });
        }
    },
    
}
</script>
